/* eslint-disable */


import './App.css';
import React, {useState} from 'react';

function App() {
  let [제목, 제목변경] = useState(['오늘의 날씨','내일의 날씨','모레의 날씨']);
  let [따봉, 따봉변경] = useState(0);
  let [modal, modal변경] = useState(false);


  function 제목바꾸기 (){
    var newArray = [...제목];
    newArray.sort();
    제목변경( newArray );
  }
  return (
    <div className="App">
      <div className="black-nav">
        Instagram
      </div>
      <h3>블로그 글</h3>
      <button onClick = { 제목바꾸기 }>버튼</button>
      <div className = "list">
        <p>{제목[0]}<span onClick={ ()=>{따봉변경(따봉+1)} }>👍</span>{따봉}</p>
        <p>2020년 6월 15일</p>
        <hr/>
      </div>

      {/* 2 */}

      <div className = "list">
        <p>{제목[1]}</p>
        <p>2020년 6월 16일</p>
        <hr/>
      </div>

      {/* 3 */}
      <div className = "list">
        <p>{제목[2]}</p>
        <p>2020년 6월 17일</p>
        <hr/>
      </div>

      <button onClick = { ()=>{modal변경(!modal)} }>모달 창</button>

      {
        modal === true
        ? <Modal />
        : null
      }
      


    </div>
  );
}

function Modal(){
  return (
    <div className = "modal">
        <h2>제목</h2>
        <p>날짜</p>
        <p>상세내용</p>
      </div>
  )
}


export default App;
